.container {
  display: flex;
  flex-direction: column;
  height: 400px;
  width: 685px;
  background-color: rgba(13, 13, 14, 0.835);
}
.mini-img {
  height: 60px;
  width: 60px;
  border-radius: 50%;
  padding: 10px;
}
.main-img img {
  height: 290px;
  border-radius: 20px;
  background: rgb(236, 236, 233);
  width: 85%;
  margin-left: 13%;
}
.header-container {
  display: flex;
}
.wrapper {
  display: flex;
  flex-direction: column;
  color: white;
  padding: 10px;
}
#author-name {
  font-weight: bold;
}

#date {
  padding: 10px;
  opacity: 0.4;
}

#author-nickname {
  opacity: 0.4;
}

.footer-icons {
  height: 20px;
  
}
.footer-icons {
  color: white;
  display: flex;
  justify-content: space-between;
  padding-left: 110px;
  padding-right: 100px;
}
.icons{
  font-size: 10px;
  padding-left: 4px;
  padding-bottom: 20px;
}

